import { Container } from "@mui/material";
import ViewItems from "./common/ViewItems";
import Navigation from "./common/Navigation";
import FormBudget from "./common/FormBudget";
import { useFormBudget } from "./common/useFormBudget";
import Result from "./common/Result";

const App = () => {
  const { listItems, removeItem, fields, handleFieldChange, handleSubmit, validationAmount, result } = useFormBudget();

  return (
    <>
      <Navigation />
      <Result result={result} fields={fields} />
      <Container maxWidth="md">
        <FormBudget
          listItems={listItems}
          removeItem={removeItem}
          fields={fields}
          handleFieldChange={handleFieldChange}
          handleSubmit={handleSubmit}
          validationAmount={validationAmount}
        />
        <ViewItems
          listItems={listItems}
          removeItem={removeItem}
          currency={fields.currency}
        />
      </Container>
    </>
  );
}

export default App;
