import { useFormFields } from "../utils/useFormFields";
import { nanoid } from "nanoid";
import { useLocalStorage } from "../utils/useLocalStorage";

export const useFormBudget = () => {
    const [listItems, setListItems] = useLocalStorage("listItems");
    const [result, setResult] = useLocalStorage("result");

    const { fields, setFields, handleFieldChange } = useFormFields({
        description: '',
        category: '',
        variant: '',
        amount: '',
        date: '',
        currency: 'PLN',
    });

    const { description, category, variant, amount, date, currency } = fields;

    const formattedDate = new Date(date).toLocaleDateString('pl-PL', { year: 'numeric', month: '2-digit', day: '2-digit' });

    const formattedAmount = Number(amount).toFixed(2);

    const validationAmount = (event) => {
        if (event.key === "-") {
            event.preventDefault();
        }
    };

    const addItem = () => {
        const newItem = { description: description, category: category, variant: variant, amount: formattedAmount, date: formattedDate, currency: currency, id: nanoid() };
        setListItems(prevItem => ([...prevItem, newItem]));
        console.log(description, category, variant, formattedAmount, formattedDate, currency);
    };

    const removeItem = (id) => {
        setListItems(listItems.filter((item) => item.id !== id));
    };

    const handleResult = () => {
        const today = new Date();
        const entryDate = new Date(date);

        if (entryDate <= today) {
            if (variant === "expense") {
                setResult(prevResult => (prevResult - Number(amount)));
            } else {
                setResult(prevResult => (prevResult + Number(amount)));
            }
        } else {
            console.log("Data jest pniejsza ni dzisiaj. Nie uwzgldniono wpisu.");
        }
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        handleResult();
        addItem();
        setFields({
            description: '',
            category: '',
            variant: '',
            amount: '',
            date: '',
            currency: currency,
        });
    };

    return { handleSubmit, listItems, removeItem, fields, handleFieldChange, validationAmount, result, handleResult }
};

